  .about__advantages li span {
    max-width: 100%;
    font-size: 16px;
    line-height: 20px;
  }
  .about__advantages li p {
    max-width: 100%;
    font-size: 14px;
    line-height: 20px;
  }
}
.programs {
  padding-bottom: 131px;
}

.programs__container {
  padding: 60px 0 0;
  max-width: 1240px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.programs__top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 40px;
  order: 1;
}
.programs__top h2 {
  margin: 0;
  font-size: 50px;
  line-height: 60px;
  color: #316dc2;
  font-weight: 400;
}

.programs__control {
  position: relative;
  order: 3;
  display: flex;
  width: 100%;
  max-width: 1270px;
  padding: 0 15px;
  margin: 0 auto;
}

.programs__scrollbar {
  width: 100%;
  height: 2px;
  background-color: rgba(49, 109, 194, 0.2);
  cursor: pointer;
}

.swiper-scrollbar-drag {
  background-color: #316dc2;
  border-radius: 0;
}

.programs__buttons {
  position: absolute;
  display: flex;
  gap: 16px;
  background-color: transparent;
  z-index: 2;
  right: 15px;
  top: 50%;
  background-color: #f0efe9;
  padding-left: 52px;
  transform: translate(0, -50%);
}

.programs__button {
  width: 22px;
  height: 22px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 50%;
}
.programs__button--prev svg {
  color: #316dc2;
  width: 9px;
  height: 9px;
  transform: rotate(-135deg);
}
.programs__button--next svg {
  width: 9px;
  height: 9px;
  color: #316dc2;
  transform: rotate(45deg);
}
.programs__button:hover {
  background-color: #ffffff;
}
.programs__button:focus {
  background-color: #ffffff;
}
.programs__button:active {
  background-color: #ffffff;
}
.programs__button:active svg {
  opacity: 70%;
}
.programs__button:disabled {
  background-color: transparent;
  cursor: default;
}
.programs__button:disabled svg {
  color: #a9afb7;
}

.programs__slider {
  order: 2;
  max-width: 1270px;
  margin: 0 auto;
  margin-bottom: 40px;
  padding: 0 15px;
}

.programs__slide {
  position: relative;
  max-width: 392px;
  min-height: 420px;
  padding: 20px;
  display: flex;
  align-items: flex-end;
  height: auto;
  border-radius: 12px;
  background-size: cover;
}
.programs__slide::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(49, 109, 194, 0.2);
  z-index: 2;
  bottom: 0;
  left: 0;
  border-radius: 12px;
}

.programs__slide--internship {
  background-image: url("../../img/programs/internship.jpg");
}

.programs__slide--volunteering {
  background-image: url("../../img/programs/volunteering.jpg");
}

.programs__slide--education {
  background-image: url("../../img/programs/education.jpg");
}

.programs__text {
  max-width: 321px;
  min-height: 120px;
  display: flex;
  flex-direction: column;
  z-index: 3;
}
.programs__text h3 {
  position: relative;
  min-width: 194px;
  background-color: #ffffff;
  border-radius: 12px 12px 0 0;
  margin: 0;
  width: fit-content;
  text-align: start;
  padding: 9px 12px 6px 18px;
  font-size: 24px;
  line-height: 26px;
  color: #316dc2;
  font-weight: 400;
}
.programs__text h3::after {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  background-image: url("../../img/svg/pagination-angle.svg");
  right: -18px;
  bottom: 0;
}
.programs__text div {
  display: flex;
  padding: 5px 18px;
  background-color: #ffffff;
  border-radius: 0 12px 12px 12px;
  gap: 16px;
  align-items: center;
  min-height: 79px;
}
.programs__text p {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  max-width: 239px;
}
.programs__text a {
  display: flex;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #316dc2;
  justify-content: center;
  align-items: center;
}
.programs__text a:hover {
  background-color: #ffffff;
}
.programs__text a:hover svg {
  color: #316dc2;
  transform: rotate(45deg);
}
.programs__text a:focus {
  background-color: #ffffff;
}
.programs__text a:focus svg {
  color: #316dc2;
  transform: rotate(45deg);
}
.programs__text a:active {
  background-color: #ffffff;
}
.programs__text a:active svg {
  color: rgba(49, 109, 194, 0.7);
  transform: rotate(45deg);
}
.programs__text svg {
  color: #ffffff;
}

@media (max-width: 1199px) {
  .programs__container {
    padding: 50px 30px 0;
    max-width: 100%;
    margin: 0;
  }
  .programs__top h2 {
    font-size: 34px;
    line-height: 50px;
  }
  .programs__control {
    max-width: 100%;
    padding: 0 45px;
    margin: 0;
  }
  .programs__buttons {
    gap: 10px;
    right: 45px;
    padding-left: 64px;
  }
  .programs__slider {
    max-width: 100%;
    margin: 0;
    margin-bottom: 40px;
    padding: 0 0 0 45px;
  }
  .programs__slide {
    max-width: 324px;
  }
  .programs__slide--internship {
    background-position: center;
  }
  .programs__slide--volunteering {
    background-position: center;
  }
  .programs__slide--education {
    background-position: center;
  }
  .programs__text {
    max-width: 263px;
    min-height: 115px;
  }
  .programs__text h3 {
    min-width: 173px;
    padding: 9px 12px 6px 18px;
    font-size: 20px;
    line-height: 24px;
  }
  .programs__text div {
    min-height: 77px;
  }
  .programs__text p {
    margin: 0;
    font-size: 14px;
    line-height: 20px;
    max-width: 181px;
    letter-spacing: -0.4px;
  }
}
@media (max-width: 767px) {
  .programs {
    position: relative;
  }
  .programs__container {
    padding: 33px 0 0;
  }
  .programs__top {
    align-items: center;
    margin-bottom: 30px;
  }
  .programs__top h2 {
    font-size: 20px;
    line-height: 20px;
  }
  .programs__link {
    position: absolute;
    bottom: 0;
    width: 203px;
  }
  .programs__buttons {
    gap: 8px;
    right: 15px;
    padding-left: 0;
    background-color: transparent;
    top: 33px;
    transform: none;
  }
  .programs__control {
    padding: 0;
    position: static;
  }
  .programs__scrollbar {
    display: none;
  }
  .programs__slider {
    max-width: 100%;
    margin: 0;
    margin-bottom: 72px;
    padding: 0 15px;
  }
  .programs__slide {
    padding: 10px;
    max-width: 100%;
    min-height: 400px;
  }
  .programs__slide--internship {
    background-position: inherit;
  }
  .programs__slide--volunteering {
    background-position: inherit;
  }
  .programs__slide--education {
    background-position: inherit;
  }
  .programs__text {
    max-width: 100%;
    min-height: 111px;
  }
  .programs__text h3 {
    min-width: 146px;
    font-size: 16px;
    line-height: 18px;
  }
  .programs__text div {
    min-height: 69px;
    width: 100%;
    max-width: 100%;
  }
  .programs__text p {
    margin: 0;
    font-size: 12px;
    line-height: 14px;
    max-width: 188px;
    letter-spacing: -0.4px;
  }
}
